@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@900&display=swap);

body{
    font-family: 'Raleway', sans-serif !important;
    padding:0;
    margin:0;
    overflow-x: hidden;
}

.navbar{box-shadow:0px 0px 10px 0px rgba(0,0,0,0.2)}.nav-item{margin:0px 8px 0px 8px !important}.nav-item .nav-link{color:#999999 !important}.navbar-brand{margin-left:100px}.navbar-brand img{height:40px;width:130px;padding:0;margin:0}.gradient-button{background:linear-gradient(to right, #a10679, #a80e77, #b81e71, #cd2f69, #e23f60, #f74f57, #fb5256) !important;border:none !important;border-radius:25px !important;width:120px !important}

.header-textarea{margin:65px 0px 0px 0px}.header-textarea h4{font-weight:600;font-size:36px;line-height:42px;vertical-align:baseline;letter-spacing:normal;word-spacing:1px;color:rgba(78,77,77,0.993);font-weight:600;font-style:normal;-webkit-font-feature-settings:normal;font-feature-settings:normal;font-variant:normal;text-transform:none;-webkit-text-decoration:none solid #666;text-decoration:none solid #666;text-indent:0px;margin-bottom:30px}.header-textarea p{color:#838383;font-size:18px;line-height:25px;vertical-align:baseline;letter-spacing:normal;word-spacing:3px;font-weight:500;font-style:normal;-webkit-font-feature-settings:normal;font-feature-settings:normal;font-variant:normal;text-transform:none;-webkit-text-decoration:none solid #838383;text-decoration:none solid #838383;text-indent:0px}.header-textarea h6{color:#838383;font-size:12px;line-height:35px;vertical-align:baseline;letter-spacing:normal;word-spacing:3px;font-weight:500;font-style:normal;-webkit-font-feature-settings:normal;font-feature-settings:normal;font-variant:normal;text-transform:none;-webkit-text-decoration:none solid #838383;text-decoration:none solid #838383;text-indent:0px}.header-imgarea{padding-top:65px}.headerbutton{background:linear-gradient(to right, #a10679, #a80e77, #b81e71, #cd2f69, #e23f60, #f74f57, #fb5256) !important;border:none !important;border-radius:25px !important;padding:7px 20px 7px 20px}

.academic-header{text-align:center;margin:100px 25px}.academic-header h4{font-weight:600;font-size:32px;vertical-align:baseline;word-spacing:0px;color:rgba(78,77,77,0.993);font-weight:600;-webkit-text-decoration:none solid #666;text-decoration:none solid #666;margin-bottom:10px}.academic-header p{color:#838383;font-size:16px;line-height:25px;vertical-align:baseline;word-spacing:0px;font-weight:500;-webkit-text-decoration:none solid #838383;text-decoration:none solid #838383}.institution-info img{-webkit-transform:scale(0.8);transform:scale(0.8)}.institution-info h6{color:rgba(78,77,77,0.993);margin-top:15px;font-weight:700;word-spacing:1px}.institution-info p{color:#838383;font-weight:500}.align-center{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.academicsec-card{background:#f2f5f9;width:100%;border-radius:10px;height:200px}.academicsec-card img{-webkit-transform:scale(0.9);transform:scale(0.9)}.academicsec-card p{font-weight:600;color:#8c8d8e}.academicsec-card h6{font-weight:700;color:#61696c}.details-section{margin:100px 0px}.details-section h6{font-weight:700;color:#414a4d;font-size:16px;line-height:25.4px}.details-section p{color:#838383;font-weight:500;font-size:14px;line-height:23px;word-spacing:1.5px}.details-section p span{font-weight:600}.details-section p:first-of-type{margin-top:20px}

.one-line-card{background:#f5f8fb;padding:10px;border-radius:30px;font-weight:600;font-size:12px;color:#5c5c5d}.banner-image{margin-top:10px;margin-bottom:100px;height:350px;width:100vw;opacity:0.9}.align-center-column{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.online-scheduling-software img{margin-top:10px;padding:30px}.online-scheduling-software h6{font-weight:700;font-size:16px;color:#61696c}.online-scheduling-software p{font-size:12px;font-weight:500;color:#8c8d8e;text-align:center;word-spacing:2px}.ai-button{text-align:center;border:2px solid #e16084;border-radius:30px;color:#e16084;font-weight:500}

.comment-card{border-radius:5px;border:1px solid #e5e5e6;position:relative;height:350px;padding:50px;font-weight:600;font-size:16px}.comment-card::after{content:"";position:absolute;top:-7%;left:0;height:50px;width:50px;background:url(/static/media/Path-19-1.ca39ccf3.svg);z-index:1}.rating-holder{margin:100px 0px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.rating-holder img{height:40px;width:100px}.peace-of-mind-holder{background:#f2f5fa}.peace-of-mind-card{background:#fff;margin:10px;padding:20px;width:500px;height:200px}.peace-of-mind-card h6{font-weight:700;font-size:16px}.peace-of-mind-card p{font-size:14px;font-weight:500}

.footer-gradient-button{background:linear-gradient(to right, #dc3662, #ec445c, #f34b59, #fd5355);border:none;border-radius:30px;padding:10px 20px}.footer-button-text{color:#7f7f7f;font-weight:500;font-size:12px}.footer-image{width:100%;height:300px;padding:0;margin:0}.footer-bar{background:#edf1f6;margin:0px 0px;padding:100px}.footer-bar h6{color:#060606;font-weight:700}.footer-bar ul{padding:0;margin:0;list-style:none}.bottom-footer{height:50px;width:100%;color:#5f5f60;background-color:#060606;text-align:center;line-height:50px}

